.sidebar {
    position: fixed;
    width: 317px;
    height: 100%;
    background-color: #FFFFFF;
    z-index: 20;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
}

.sidebar.open {
    transform: translateX(0);
}

.background {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 15;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0s 0.3s;
}
.background.visible {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.3s ease, visibility 0s 0s;
}

.nav {
    display: flex;
    flex-direction: column;
    padding: 23px 0 0 70px;
}

.nav a {
    padding-bottom: 45px;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-weight: 600;
    line-height: 50%;
    color: #000000;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    max-width: 24px;
    max-height: 24px;
    padding: 15px 25px 25px 270px;
    cursor: pointer;
}

