.burgerMenuIcon {
    width: 24px;
    height: 24px;
    margin-left: 70px;
    cursor: pointer;
}

.header {
    position: fixed;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 40px 0 #1D212621;
    z-index: 10;
    background-color: #FFFFFF;
}

.header h1 {
    font-family: Montserrat, sans-serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 55%;
    margin-right: 70px;
}
.pagesBlock {
    padding-top: 100px;
}