.friendMessage {
}

.friendImageAndText {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    padding: 20px;
}

.friendImageAndText img {
    width: 40px;
    height: 40px;
    margin: 5px;
}

.friendText {
    color: #000;
    background-color: #F5F7FB;
    border-radius: 10px;
    padding: 5px 15px;
}

.friendText:after {

}

.friendName {
    font-size: 17px;
    text-align: left;
}

.friendMessageText {
    font-size: 15px;
    text-align: center;
    margin: 5px 0;
}

.friendTimeAndPhoto {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 12px;
    color: #000;
    margin-top: 5px;
}
